.next {
    height: 11vh;
    width: 80%;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 32px;
    font-weight: 900;
    outline: 2px solid black;
    background-color: #f1f1f1; }
.ProjectCardWrapper {
    width: 100%;
    margin-bottom: 10vh;
    display: flex;
    justify-content: center;
    .ProjectCard {
        width: 80%;
        outline: 2px solid black;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        .links {
            width: 100%;
            outline: 2px solid;
            display: flex;
            justify-content: space-between;
            cursor: pointer;
            a {
                text-decoration: none;
                color: black;
                width: 50%;
                height: 15vh;
                background-color: white;
                transition: 0.3s;
                cursor: pointer;
                font-size: 32px;
                font-weight: 900;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: #f1f1f1; }
            a:hover {
                background-color: #858585; } } } }

