.slider {
    margin-top: 11vh;
    width: 80vw;
    height: 45vw;
    display: flex;
    justify-content: space-between;
    outline: 2px solid black;
    background-size: cover !important;
    span {
        -moz-user-select: none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        user-select: none;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 32px;
        font-weight: 900;
        width: 50%;
        cursor: pointer;
        transition: 0.3s;
        opacity: 0;
        color: white; }
    span:hover {
        opacity: 1;
        background-color: rgba(0, 0, 0, 0.4); } }
